// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - Footer

.footer {
	@include transition(all 0.4s ease-in-out 0.8s);
	padding: 20px;

	@include mq(tabletp) {
		padding: 40px;
	}

	@include mq(tabletl) {
		display: none;
	}
}

.footer__copyright {
	display: block;
	margin-top: 5px;
	font-size: 13px;
	line-height: 1.5;
	color: $text-light-color;

	a {
		color: $text-light-color;
		font-weight: $regular-weight;

		&:hover {
			color: $text-medium-color;
		}
	}
}
